<template>
  <div>
    <div class="body">
      <!-- 工会log -->
      <div class="log">
        <img src="../../../static/img/2.png" />
      </div>
      <!-- 手机号 -->
      <form class="fl-ex ju-ar al-it fl-co">
        <div class="input fl-ex ju-ar al-it">
          <img src="../../../static/img/4.png" />
          <a>
            <input 
               type="tel" 
               placeholder="请输入手机号" 
               v-model="phoneValue" 
               @blur="animate()" 
               max="11" 
            />
          </a>
        </div>
        <div class="input fl-ex ju-ar al-it">
          <img src="../../../static/img/3.png" />
          <a>
            <input
              type="password"
              placeholder="请输入密码"
              v-model="password"
              @blur="mima"
              min="6"
              max="16"
            />
          </a>
        </div>
      </form>
      <!-- 登录，注册 -->
      <div class="login fl-ex ju-ar al-it fl-co">
        <a @click="$router.push('/My-Self')">登录</a>
        <a>注册</a>
      </div>
      <!-- 忘记密码 -->
      <div class="forget">
        <u>忘记密码?</u>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phoneValue: "",
      password: ""
    };
  },
  methods: {
    animate() {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(this.phoneValue)){
        alert("请输入正确的手机号");
        this.phoneValue = "";
      }
    },
    mima: function() {
      if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/.test(this.password)){
        alert("请输入6-16字母数字组合的密码");
        this.password = "";
      }
    }
  }
};
</script>

<style scoped>
.body {
  width: 100%;
  height: 18rem;
  background: url("../../../static/img/1.jpg") no-repeat;
  background-size: 100% 38rem;
}
.body .log {
  width: 70%;
  height: 15.5rem;
  margin: 0 auto;
  position: relative;
}
.body .log img {
  width: 39%;
  height: 40.5%;
  text-align: center;
  margin: 15% 30%;
  position: absolute;
}
.body form {
  width: 100%;
  height: 8rem;
}
.body form .input {
  width: 85%;
  height: 3.5rem;
  margin: 0 auto;
}
.body form .input img {
  width: 6.8%;
  height: 1.37rem;
}
.body form .input a {
  width: 79%;
  height: 2.5rem;
  display: inline-block;
  border-bottom: 1.05px solid #cfcfcf;
}
.body form .input a input {
  width: 100%;
  height: 2.3rem;
  outline: none;
  border: none;
  font-size: 0.9rem;
  color: #8b8b8b;
}
.body .login {
  width: 100%;
  height: 8.5rem;
}
.body .login a:nth-child(1) {
  width: 80%;
  height: 3rem;
  background-color: #df0001;
  display: block;
  margin: 0 auto;
  border-radius: 0.25rem;
  font-size: 1.1rem;
  color: white;
  text-align: center;
  line-height: 3rem;
  font-weight: bold;
}
.body .login a:nth-child(2) {
  width: 80%;
  height: 2.7rem;
  background-color: white;
  display: block;
  margin: 0 auto;
  border-radius: 0.25rem;
  font-size: 1.1rem;
  color: black;
  text-align: center;
  line-height: 2.7rem;
  border: 0.074rem solid #888888;
  font-weight: bold;
}
.body .forget {
  width: 100%;
  height: 2.5rem;
}
.body .forget u {
  font-size: 0.97rem;
  text-align: center;
  line-height: 2.5rem;
  color: #888888;
}
</style>


